@import url(https://fonts.googleapis.com/css2?family=Inconsolata:wght@200;300;400;500;600;700;800;900&display=swap);
/* Global CSS */

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.container {
  height: 100%;
  width: 100%;
  max-width: 1092.8px;
  display: flex;
  align-items: center;
}
.App {
  text-align: center;
  max-width: 100vw;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.navigation__wrapper {
    background-color: transparent;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    max-width: 100vw;
    width: 100%;
    height: 75px;
    z-index: 1;
}

.navbrand {
    margin-right: auto;
    padding: 1rem;
}

ul {
    display: inline-flex;
    list-style: none;
}

li {
    padding: 1rem;
}

a {
    color: white;
    text-decoration: none;
    font-family: 'Inconsolata', monospace;
    letter-spacing: 3px;
    text-transform: uppercase;
    font-weight: 300;
}
.header__wrapper {
    background-color: #151515;
    height: 100vh;
    width: 100%;
    max-width: 100vw;
    margin-top: -75px;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    overflow: hidden;
}
.earth__back {
    width: 500px;
    background-color: aliceblue;
    background-image: url(/react-site/static/media/earth.82d2ef9b.jpg);
    background-size: cover;
    height: 500px;
    position: absolute;
    bottom: 0;
    right: 0;
    margin: auto -8rem -8rem auto;
    border-radius: 50%;
    box-shadow: inset 0 0 40px #03a9fa,
                0 0 100px #03a9fa;
    -webkit-animation: Rotation 25s linear infinite;
            animation: Rotation 25s linear infinite;
}

@-webkit-keyframes Rotation {
    0% {
        background-position: 0%;
    }
    100% {
        background-position: 162.5%;
    }
}

@keyframes Rotation {
    0% {
        background-position: 0%;
    }
    100% {
        background-position: 162.5%;
    }
}


.stars {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 4px;
    height: 4px;
    background-color: aliceblue;
    border-radius: 50%;
    box-shadow: 0 0 0 1px rgba(255, 255, 255, 0.1),
    0 0 0 3px rgba(255, 255, 255, 0.1),
    0 0 0 10px rgba(255, 255, 255, 0.1);
    -webkit-animation: starshoot 3s linear infinite;
            animation: starshoot 3s linear infinite;
}
.stars::before {
    content: '';
    position: absolute;
    top:50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    width: 300px;
    height: 1px;
    background: linear-gradient(90deg, #fff, transparent);
}
@-webkit-keyframes starshoot {
    0% {
        -webkit-transform: rotate(215deg) translate(0);
                transform: rotate(215deg) translate(0);
        opacity: 0;
    }
    70% {
        opacity: 1;
    }
    100% {
        -webkit-transform: rotate(215deg) translate(-1000px);
                transform: rotate(215deg) translate(-1000px);
        opacity: 0;
    }
}
@keyframes starshoot {
    0% {
        -webkit-transform: rotate(215deg) translate(0);
                transform: rotate(215deg) translate(0);
        opacity: 0;
    }
    70% {
        opacity: 1;
    }
    100% {
        -webkit-transform: rotate(215deg) translate(-1000px);
                transform: rotate(215deg) translate(-1000px);
        opacity: 0;
    }
}

.stars:nth-child(2) {
    top: 0;
    left: 0;
    -webkit-animation-delay: 0;
            animation-delay: 0;
    -webkit-animation-duration: 1.5s;
            animation-duration: 1.5s;
}

.stars:nth-child(3) {
    top: 30%;
    left: 15%;
    -webkit-animation-delay: 0;
            animation-delay: 0;
    -webkit-animation-duration: 2s;
            animation-duration: 2s;
}

.stars:nth-child(4) {
    top: 0%;
    left: 35%;
    -webkit-animation-delay: 0;
            animation-delay: 0;
    -webkit-animation-duration: 4s;
            animation-duration: 4s;
}

.stars:nth-child(5) {
    top: 0%;
    left: 65%;
    -webkit-animation-delay: 0;
            animation-delay: 0;
    -webkit-animation-duration: 3s;
            animation-duration: 3s;
}

.stars:nth-child(6) {
    top: 0%;
    left: 45%;
    -webkit-animation-delay: 0;
            animation-delay: 0;
    -webkit-animation-duration: 1.5s;
            animation-duration: 1.5s;
}

.stars:nth-child(7) {
    top: 0%;
    left: 50%;
    -webkit-animation-delay: 0;
            animation-delay: 0;
    -webkit-animation-duration: 2.5s;
            animation-duration: 2.5s;
}
.window {
    display: flex;
    flex-direction: column;
    margin-left: -35%;
    z-index: 5;
}

.window__header {
    display: flex;
    flex-direction: row;
    width: 300px;
    height: 30px;
    background-color: rgba(255, 255, 255, 0.7);
    align-items: center;
    padding-left: .75rem;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
}

.dot__1,
.dot__2,
.dot__3 {
    margin-left: .25rem;
    border-radius: 50%;
    height: 10px;
    width: 10px;
    background-color: #333333;
}

.dot__2 {
    background-color: #666666;
}

.dot__3 {
    background-color: #999999;
}

.window__body {
    padding: 1rem;
    width: 300px;
    height: auto;
    background-color: rgba(255, 255, 255, 0.5);
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
}

@media screen and (max-width: 480px) {
    .window {
        margin-left: 0;
    }
}
